import React from 'react';
import { graphql } from 'gatsby';
import Layout from '../components/layout';
import '../components/bootstrap.css';
import {Row,Col} from "reactstrap";
import {ThemeToggler} from "gatsby-plugin-dark-mode";
import Toggle from "react-toggle";


const BlogTemplate = ({ data }) => {
    const { markdownRemark } = data;
    const { frontmatter, html } = markdownRemark;

    function handleLink(e) {
        e.preventDefault();
        window.location.replace("/blogs");
    }
    return (
        <ThemeToggler>
            {({ theme, toggleTheme }) => (
        <Layout >
            <h1 className="blogHeader">
                <span onClick={handleLink} >Aman Dave</span>
                <div style={{ float: "right", fontSize: "20px", paddingTop: "10px"}}>
                    <label>
                        <Toggle
                            className='dark-theme'
                            onChange={e => toggleTheme(e.target.checked ? 'dark' : 'light')}
                            value={theme === 'dark'}
                            icons={{
                                checked: <img src={require('../images/sun.png')} height="20px" style={{marginTop :"-5px", marginLeft: "-5px"}} />,
                                unchecked:<img src={require('../images/moon.png')} height="20px" style={{marginTop :"-5px", marginLeft: "-5px"}}  />
                            }}/>
                    </label>
                </div>
            </h1>
            <section>
                <div className="blogTitle">
                    <h1>{frontmatter.title}</h1>
                    <span>{frontmatter.date}</span>
                </div>
                <br/>
                <hr/>
                <br/>
                <div className="blogText"
                     dangerouslySetInnerHTML={{ __html: html }} />
            </section>
        </Layout>
            )}
        </ThemeToggler>
    );
};

export default BlogTemplate;

export const pageQuery = graphql`
  query($slug: String!) {
    markdownRemark(fields: { slug: { eq: $slug } }) {
      html
      frontmatter {
        date(formatString: "MMMM DD, YYYY")
        title
      }
    }
  }
`;
